.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
  margin-bottom: 50px;
  padding-left: 5rem;
  padding-right: 5rem;
}

.footer_contacts {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  width: 100%;
  color: #42446e;
}

.footer_line {
  border-top: 2px solid #a6a4a4;
  width: 100%;
  margin: 2rem;
  display: flex;
  justify-content: center;
}

.footer_content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
}

.footer_nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  width: 100%;
  color: #42446e;
}

.footer_subContent {
  width: 100%;
  text-align: right;
  margin-right: -5px;
}

.footer_subContentDesign {
  padding-left: 5px;
  padding-right: 5px;
  background: -webkit-linear-gradient(45deg, #13b0f5, #e70faa);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Media Queries for responsiveness */

/* Tablets and larger smartphones */
@media (max-width: 992px) {
  .footer_contacts {
    flex-direction: column;
    text-align: center;
  }

  .footer_nav {
    flex-direction: column;
    text-align: center;
  }

  .footer_content {
    flex-direction: column;
  }

  .footer_subContent {
    text-align: center;
    margin-right: 0;
  }
}

/* Smaller tablets and larger smartphones */
@media (max-width: 768px) {
  .footer {
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .footer_logo {
    width: 100px;
  }

  .footer_socialIcons {
    width: 40px;
    height: 40px;
  }

  .footer_nav {
    flex-direction: column;
    text-align: center;
  }

  .footer_subContent {
    margin: 1rem 0;
    text-align: center;
  }
}

/* Mobile phones */
@media (max-width: 576px) {
  .footer {
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .footer_logo {
    width: 80px;
  }

  .footer_socialIcons {
    width: 30px;
    height: 30px;
  }

  .footer_contacts {
    flex-direction: column;
    align-items: center;
  }

  .footer_nav {
    flex-direction: column;
    text-align: center;
    margin-top: 1rem;
  }

  .footer_subContentDesign {
    font-size: 0.9rem;
  }

  .footer_line {
    margin: 1rem;
  }
}
